.widgets{
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 10px;
    -webkit-box-shadow:2px 4px 10px 1px rgba(0,0,0,0.47);
    box-shadow: 2px 4px 10px 1px rgba(201,201,201,0.47);
    border-radius: 10px;
    height: 6rem;


    .left, .right{
        display: flex;
        flex-direction: column;
        justify-content: space-between;


        .title{
            font-weight: bold;
            font-size: 1rem;
            color: rgb(160, 160, 160);
        }

        .counter{
            font-size: 1.8rem;
            font-weight: 300;
        }


        .link{
            width: max-content;
            font-size: 12px;
            font-weight: 500;
            border-bottom: 1px solid gray;
        }

        .percentage{
            display: flex;
            align-items: center;
            font-size: 1rem;



            &.positive{
               color: green;
                
            }
            &.nagative{
               color: red;

            }
        }

        .icon{
            font-size: 18px;
            padding: 5px;
            // background-color: rgba(227, 115, 227, 0.541);
            border-radius: 5px;
            align-self: flex-end;
        }
    }

}